<template>
    <div>
      <div v-if="desmosId === 1 && desmosUrls.length === 0">
        <DesmosComp
          :calcId="1"
          @click.native="$emit('on-click-to-desmos', $event, 1)"
          @keyup.native="$emit('on-click-to-desmos', $event, 1)"
        ></DesmosComp>
      </div>
      
      <div
        v-else
        :class="
          !codeIsRunning
            ? (desmosGrid === 'desmos' ? 'desmos-row' : (
              desmosGrid === 'normal' ? 'normal-row' + desmosUrls.length : 'vertical-row' + desmosUrls.length
            ))
            : (activeDesmosIds.length === 1 ? 'desmos-row' : (
              desmosGrid === 'normal' ? 'normal-row' + desmosUrls.length : 'vertical-row' + desmosUrls.length
            ))
          "
      >
      <div
        v-for="(desmos, index) in desmosUrls"
        :key="index"
      >
        <DesmosComp
          :calcId="index+1"
          v-show="
            (!codeIsRunning && (desmosGrid === 'normal')) ||
            (!codeIsRunning && (desmosGrid === 'vertical')) ||
            (!codeIsRunning && desmosGrid === 'desmos' && desmosId === index+1) ||
            (codeIsRunning && activeDesmosIds[0] ? activeDesmosIds.includes(index+1): null)"
          @click.native="$emit('on-click-to-desmos', $event, index+1)"
          @keyup.native="$emit('on-click-to-desmos', $event, index+1)"
        ></DesmosComp>
      </div>
      </div>
    </div>
</template>
<script>
export default {
  name: "DisplayComp",
  data: () => ({
  }),
  props: {
    codeIsRunning: {
      type: Boolean,
    },
    desmosId: {
      type: Number,
      defaultValue: 1,
    },
    activeDesmosIds: {
      type: Array,
      defaultValue: [1],
    },
    desmosUrls: {
      type: Array,
    },
    desmosGrid: {
      type: String,
      defaultValue: 'normal'
    }
  },
  components: {
    DesmosComp: () => import('../components/desmos-comp.vue'),
  },
  methods: {

  },
}
</script>

<style>
.desmos-row {
  display: grid;
  grid-template-columns: auto;
  grid-gap: 5px;
}
.normal-row1 {
  display: grid;
  grid-template-columns: auto;
  grid-gap: 5px;
}
.normal-row2 {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 5px;
}
.normal-row3 {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 5px;
}
.normal-row4 {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 5px;
}
.normal-row5 {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  grid-gap: 5px;
}

.vertical-row1 {
  display: grid;
  grid-template-rows: auto;
  grid-gap: 5px;
}
.vertical-row2 {
  display: grid;
  grid-template-rows: repeat(2, 38vh);
  grid-gap: 5px;
}
.vertical-row3 {
  display: grid;
  grid-template-rows: repeat(3, 25vh);
  grid-gap: 5px;
}
.vertical-row4 {
  display: grid;
  grid-template-rows: repeat(4, 19vh);
  grid-gap: 5px;
}
.vertical-row5 {
  display: grid;
  grid-template-rows: repeat(5, 15vh);
  grid-gap: 5px;
}
</style>
